/*
app__aboutus
  app__aboutus-overlay
    img
  app__aboutus-content
    app__aboutus-content_about
      h1
      img
      p
      button
    
    app__aboutus-content_knife
      img
    
    app__aboutus-content_history
      h1
      img
      p
      button
*/

.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 391px;
    /* height: 415px; */
    z-index: 0;
}

.app__aboutus-content {
    /* width: 100%; */
    z-index: 2;
}

.app__aboutus-content_about {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    /* flex: 1; */
    text-align: right;   
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
    
}

.app__aboutus-content_knife img {
    height: 910px;
}

.app__aboutus-content_history {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    /* flex: 1; */
    text-align: left;   
}

.app__aboutus-content_history h1,
.app__aboutus-content_about h1 {
    /* font-size: 1rem; */
    /* margin: 2rem 0;
    color: var(--color-grey); */
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    /* font-size: 1rem; */
    margin: 2rem 0;
    color: var(--color-grey);
}


@media screen and (max-width: 2000px) {
    .app__aboutus-content_knife img {
        height: 1110px;
    }
    .app__aboutus-content_history p,
    .app__aboutus-content_about p {
        margin: 4rem 0;
    }
    
}

@media screen and (max-width: 900px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_knife  {
        margin: 4rem 0;
    }

    
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;

    }
}